.main-text {
  max-width: 318px;
  margin: 110px auto 0;
}

.main-text-header {
  display: flex;
  flex-direction: column;
  margin-bottom: 14px;
}

.main-text-h1 {
  font-size: 72px;
  font-weight: bold;
  letter-spacing: 0.07em;
  font-family: "Roboto", sans-serif;
  text-shadow: 0px 4.50174px 4.50174px rgba(0, 0, 0, 0.25);
}

.main-text-h2 {
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.115em;
  text-shadow: 0px 4.50174px 4.50174px rgba(0, 0, 0, 0.25);
}

.main-text-body {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.main-text-online {
  font-size: 14px;
  font-weight: 350;
  color: #83889e;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.square {
  margin-right: 5px;
  width: 6px;
  height: 6px;
  opacity: 0.6;
}

.square.online {
  background: #42a940;
  border: 0.2px solid #63C161;
}

.square.offline {
  background: #a94040;
  border: 0.2px solid #b95b5b;
}

.main-text-discord {
  font-size: 14px;
  font-weight: 350;
  color: #83889e;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.dicord-icon-mini {
  width: 16px;
  height: 11.4px;
  background: url('../../images/DiscordIconMini.svg');
  margin-left: 5px;
}

@media (max-width: 370px) {
  .main-text-h1 {
    font-size: 63px;
  }
  .main-text-h2 {
    font-size: 16px;
    white-space: nowrap;
  }
  .main-text {
    max-width: 282px;
  }
}